.App {
  width: 99vw;
  height: 99vh;
}

body {
  background: rgb(0, 36, 30);
  background: linear-gradient(
    90deg,
    rgba(0, 36, 30, 1) 1%,
    rgba(66, 217, 247, 1) 51%
  );
}

/*Navbar...*/

.btn-position {
  margin-left: 10%;
  width: 80%;
  margin-left: none;
  height: 10%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.grp-name {
  margin-top: 20px;
  font-style: Cursive;
  font-weight: 500;
  font-size: 20px;
  color: rgb(255, 166, 0);
  font-weight: bold;
}

/*styling  of the all avilable button....*/

.btn {
  height: 60%;
  margin-top: 1.1%;
  width: 8%;
  border: 3px solid rgb(4, 5, 4);
  border-radius: 10px;
  background: rgba(2, 255, 82, 0.2);
  font-weight: bold;
}

/*styling of the hover button container.*/

#button-container {
  width: 23%;
  height: 15%;
  margin-left: 39.5%;
  margin-top: 23%;
}

.hover-btn {
  height: 100%;
  width: 100%;
  font-size: 2.5rem;
}

/*styling for the top position hover...*/

.top {
  height: 30%;
  width: 50%;
  margin-top: -50%;
  margin-left: 20%;
  padding: 6px 10px 15px 10px;
  border: 1px solid black;
  border-radius: 10px;
  font-size: 1rem;
  background-color: rgba(199, 23, 79, 0.786);
}

/*styling for the right position hover...*/

.right {
  height: 30%;
  width: 50%;
  margin-top: -22%;
  margin-left: 105%;
  padding: 6px 10px 15px 10px;
  border: 1px solid black;
  border-radius: 10px;
  font-size: 1rem;
  background-color: rgba(17, 80, 197, 0.579);
}

/*styling for the down position hover...*/

.down {
  height: 30%;
  width: 50%;
  margin-top: 5%;
  margin-left: 20%;
  padding: 6px 10px 15px 10px;
  border: 1px solid black;
  border-radius: 10px;
  font-size: 1rem;
  background-color: rgba(14, 225, 95, 0.784);
}

/*styling for the left position hover...*/

.left {
  height: 30%;
  width: 50%;
  margin-top: -22%;
  margin-left: -60%;
  padding: 6px 10px 15px 10px;
  border: 1px solid black;
  border-radius: 10px;
  font-size: 1rem;
  background-color: rgba(227, 23, 23, 0.946);
}

/*styling for the active  button ...*/

.active {
  background-color: rgba(102, 4, 247, 0.3);
}
